import React, { FunctionComponent, useMemo } from 'react';

import ClassNames from './LanguageSelector.module.scss';
import classNames from 'classnames';
import { Locale } from 'Types/Locale';
import { usePageConfig } from '../../contexts/PageConfig/PageConfig';
import { getLinkPath } from '../../utils/link/link';
import AppLink from '../../util-components/AppLink/AppLink';

interface Props {
    className?: string;
    activeLinkClassname?: string;
}

export function _getNonLocaledPagePath(path: string, currentLocaleCode: string): string {
    const nonLocaledPath = path.replace(new RegExp(`^/?(${currentLocaleCode})/?`), '');
    return (nonLocaledPath.charAt(0) === '/' ? '' : '/') + nonLocaledPath;
}

export function _getUiLocales(
    locales: Locale[],
    currentLocaleCode: string,
    path: string
): { href: string; name: string; code: string }[] {
    const nonLocaledPath = _getNonLocaledPagePath(path, currentLocaleCode);
    return locales.map(locale => {
        return {
            href: getLinkPath(locale, nonLocaledPath),
            name: locale.shortName,
            code: locale.code,
        };
    });
}

const LanguageSelector: FunctionComponent<Props> = ({ className, activeLinkClassname }) => {
    const { locales, currentLocaleCode, slug } = usePageConfig();

    const parsedLocales = useMemo(_getUiLocales.bind(null, locales, currentLocaleCode, slug), []);

    return (
        <ul className={classNames(ClassNames.LanguageSelector, className)}>
            {parsedLocales.map(locale => (
                <li key={locale.code}>
                    <AppLink to={locale.href} activeClassName={activeLinkClassname}>{locale.name}</AppLink>
                </li>
            ))}
        </ul>
    );
};

export default LanguageSelector;
