import React, { FunctionComponent } from 'react';
import { Link as GatsbyLink } from 'gatsby';
import { usePageConfig } from '../../contexts/PageConfig/PageConfig';
import { Locale } from 'Types/Locale';
import { getLinkPath } from '../../utils/link/link';
import {isDev} from "../../utils/environment/environment";

// Abstraction for links, so we don't have dependency on Gatsby in our components.
// If more props needed, add them here
export interface AppLinkProps {
    activeClassName?: string;
    replace?: boolean;
    to?: string;
    toId?: string;
    className?: string;
}

const AppLink: FunctionComponent<AppLinkProps> = ({ children, to, toId, ...props }) => {
    const { pageLinkMap, locales, currentLocaleCode } = usePageConfig();

    function getLinkFromId(toId): string | null {
        const slug = pageLinkMap[toId];
        if (!slug) {
            return '';
        }
        const locale: Locale = locales.find(locale => locale.code === currentLocaleCode)!;
        return getLinkPath(locale, slug);
    }

    const internal = toId || (to && /^\/(?!\/)/.test(to));
    const link = toId ? getLinkFromId(toId) : to || '';

    if (!link) {
        if (isDev()) {
            console.warn('Missing link!');
        }
        return (
            <a {...props}>
                {children}
            </a>
        );
    } else if (internal) {
        return (
            <GatsbyLink to={link} {...props}>
                {children}
            </GatsbyLink>
        );
    } else {
        return (
            <a href={link} {...props}>
                {children}
            </a>
        );
    }
};

export default AppLink;
